<template>
  <TheTwoRowLayout
    :fullsize="true"
    :topRowClass="'h-50 align-items-center justify-content-center'"
    :bottumRowClass="'h-50 g-2 g-lg-3 align-content-center justify-content-center'"
  >
    <template #topRow>
      <div class="col-10 col-lg-4">
        <div class="display-5">{{ header1 }}</div>
        <div class="display-3" :class="textColor">{{ header2 }}</div>
        <hr />
        <div class="mt-5">
          <strong>{{ questionCounter }}</strong>
        </div>
      </div>
    </template>
    <template #bottumRow>
      <div
        v-if="resultOfAnswer === true && this.questionNumber < 10"
        class="col-10 col-lg-4 d-grid"
      >
        <button class="btn btn-primary" @click="nextQuestion()">
          Nächste Frage
        </button>
      </div>
      <div
        v-if="resultOfAnswer === false || solvedQuestions === 10"
        class="col-10 col-lg-4 d-grid"
      >
        <button class="btn btn-primary" @click="restart()">Wiederholen</button>
      </div>
      <div class="col-10 col-lg-4 d-grid">
        <button class="btn btn-primary" @click="breakUp()">Beenden</button>
      </div>
    </template>
  </TheTwoRowLayout>
</template>

<script>
import TheTwoRowLayout from "@/layouts/TheTwoRowLayout";
import Store from "../store";

export default {
  name: "WelcomeComponent",
  components: {
    TheTwoRowLayout,
  },
  data() {
    return {
      resultOfAnswer: Store.state.resultOfAnswer,
      solvedQuestions: Store.state.usedQuestions.length,
    };
  },
  computed: {
    questionNumber() {
      return Store.getters.getQuestionNumber();
    },
    header1() {
      if (this.resultOfAnswer === false) {
        return "Das war leider";
      } else if (Store.state.usedQuestions.length === 10) {
        return "Herzlichen";
      } else {
        return "";
      }
    },
    header2() {
      if (this.resultOfAnswer === true && this.questionNumber < 10) {
        return "Richtig";
      } else if (this.resultOfAnswer === false) {
        return "Falsch";
      } else {
        return "Glückwunsch";
      }
    },
    questionCounter() {
      return `Du hast ${this.questionNumber}/10 Fragen richtig beantwortet.`;
    },
    textColor() {
      if (this.resultOfAnswer === true && this.questionNumber < 10) {
        return "text-success";
      } else if (this.resultOfAnswer === false) {
        return "text-danger";
      } else {
        return "";
      }
    },
  },
  methods: {
    nextQuestion() {
      Store.mutations.setSelectedAnswer("");
      this.$router.push({ path: "/question" });
    },
    restart() {
      Store.mutations.resetQuiz();
      this.$router.push({ path: "/welcome" });
    },
    breakUp() {
      Store.mutations.setUserName("");
      Store.mutations.resetQuiz();
      this.$router.push({ path: "/" });
    },
  },
};
</script>

<style scoped></style>
